.pager {
  padding-left: 0;
  margin: 2rem 0;
  list-style: none;
  text-align: center;
}
.pager li > a {
  color: #81a5bf;
  display: inline-block;
  padding: 0.5rem 1.4rem;
  background-color: #FFFFFF;
  border: 0.1rem solid #81a5bf;
  border-radius: 0.75rem;
}
.pager li > a:hover, .pager li > a:focus, .pager li > a:active {
  color: #FFFFFF;
  background-color: #81a5bf;
}

.ajax-progress {
  display: none !important;
}

.view-portfolio .row > div {
  margin-bottom: 2rem;
}
.view-portfolio .wrapper {
  position: relative;
}
.view-portfolio .field-flex-wrap {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  background-color: transparent;
  transition: 150ms ease background-color;
}
.view-portfolio .image {
  margin-bottom: 1.5rem;
  overflow: hidden;
}
.view-portfolio img {
  transform: scale(1);
  transition: 150ms ease transform;
  width: 100%;
}
.view-portfolio .description {
  font-size: 0.9rem;
  line-height: 1.4rem;
  margin: auto;
  padding: 0 2rem;
  color: #FFFFFF;
  opacity: 0;
  text-align: center;
  transition: 150ms ease opacity;
}
.view-portfolio .title {
  margin-bottom: 0;
  color: #293749;
  font-weight: 400;
  text-align: center;
}
.view-portfolio a:hover {
  text-decoration: none;
}
.view-portfolio a:hover img {
  transform: rotate(-2deg) scale(1.05);
}
.view-portfolio a:hover .field-flex-wrap {
  background-color: rgba(24, 39, 46, 0.7);
}
.view-portfolio a:hover .description {
  opacity: 1;
}